import React, { Component } from 'react'
import { BrowserRouter, Route, Routes, Link, useParams } from 'react-router-dom'

function Home() {
  return <h3>Ini halaman home</h3>
}

function User() {
  return (
    <>
      <h3>Semua User</h3>
      <ul>
        <li><Link to="/user/shidqi">Shidqi</Link></li>
        <li><Link to="/user/rifat">Rifat</Link></li>
      </ul>
    </>
  )
}

function DetailUser() {
  const { name } = useParams()

  return <h3>Halo {name}</h3>
}

function NoMatch() {
  return <h3>404 Error Not Found</h3>
}

export default class Index extends Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <ul>
            <li><Link to='/'>Home</Link></li>
            <li><Link to='/user'>User</Link></li>
          </ul>

          <main>
            <Routes>
              <Route path='/' element={<Home />} />
              <Route path='/user' element={<User />} />
              <Route path='/user/:name' element={<DetailUser />} />
              <Route path='*' element={<NoMatch />} />
            </Routes>
          </main>
        </div>
      </BrowserRouter>
    )
  }
}
